@font-face {font-family: AvenirRegular;src: url('../assets/fonts/AvenirLTStd-Book.otf');}
@font-face {font-family: AvenirBold;src: url('../assets/fonts/AvenirLTStd-Medium.otf');}
@font-face {font-family: AvenirRoman;src: url('../assets/fonts/AvenirLTStd-Roman.otf');}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'AvenirBold';
}

h1,h2,h3,h4,h5,h6,p { margin: 0; }

input,select,button { font-family: 'AvenirBold'; }

.op_20 { opacity: 0.2 }
.op_50 { opacity: 0.5 }

.form_error { border: 1px solid red !important}

.linkblue {
  color: #008BFD !important;
  cursor: pointer;
}

.placeholder { color: rgba(0,0,0,0.4) !important; font-size: 16px !important;}

label {
  font-size: 12px;
  color: black;
}

.linkdefault {
  color: black !important;
  cursor: pointer;
}

.navimg {
  width: 34px;
  height: 34px;
}

h1 { color: rgba(0,0,0,0.52); font-size: 30px; }

.navlist {
  text-decoration: none !important;
  outline: none;
  color: rgba(0,0,0,0.8);
  font-size: 15px;
}

.navitem {
  text-decoration: none !important;
  outline: none;
}

.active {
  color: #008BFD;
}

.box_active:hover {
  background-image: linear-gradient(-135deg, #008CFD 0%, #0179E5 50%, #0259BC 100%) !important;
}

.box_active:hover>div>p,{
  color: white !important;
}

.footer {
  border-top: 1px solid #D8D8D8;
  color: #9e9e9e;
}

.footer__list {
  list-style: none;
  display: flex;
  width: 120px;
  justify-content: space-between; 
  margin: 0;
}

.footer__icon {
  width: 20px;
  height: 20px;
} 

.bg-grey {
  background-color: #D8D8D8;
}

.car-big {
  height: 150px;
  width: 150px;
}
.pulse {
    animation: pulsate 1s infinite;
}

@keyframes pulsate {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}