}

.placeholder { color: rgba(0,0,0,0.4) !important; font-size: 16px !important;}

label {
  font-size: 12px;
  color: black;
}

.linkdefault {
  color: black !important;
  cursor: pointer;
}

.navimg {
  width: 34px;
  height: 34px;
}

h1 { color: rgba(0,0,0,0.52); font-size: 30px; }

.navlist {
